img {
  display: block;
  max-width: 100%;
  height: auto;  
}
.link {
  text-decoration: none;
}
.gallery {
  padding-top: 70px;
  padding-left: 15px;
  padding-right: 15px;
  display: grid;
  grid-template-columns: repeat(4, minmax(100px, 1fr));
  // grid-template-rows: repeat(10, 20vw);
  grid-gap: 18px;
  background-color: antiquewhite;
